<script>
    import image from "bootstrap-icons/icons/blockquote-left.svg"
    export let size = 32
    export let title = 'title'
    const tag = `<svg xmlns="http://www.w3.org/2000/svg" width="${size}" height="${size}" fill="currentColor" class="bi" viewBox="0 0 16 16">` + (title ? `<title>${title}</title>` : "")
    const markup = image.replace(/<svg[^>]+>/, tag)
</script>
{@html markup}
